// Set up color style

// NEUTRAL COLOR
$darkBlue: hsl(209, 23%, 22%)           // Dark Mode Elements
$veryDarkBlueBg: hsl(207, 26%, 17%)     // Dark Mode Background
$veryDarkBlue: hsl(200, 15%, 8%)        // Light Mode Text
$darkGray: hsl(0, 0%, 52%)              // Light Mode Input
$veryLightGray: hsl(0, 0%, 98%)         //Light Mode Background
$white: hsl(0, 0%, 100%)                //Dark Mode Text & Light Mode Elements

// FONT
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,300;6..12,600;6..12,800&display=swap')

// GENERAL STYLE
*
    box-sizing: border-box
    cursor: default
    margin: 0
    padding: 0
    font-family: "Roboto"
    outline: none
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0)
    caret-color: transparent

html
    position: relative
    margin: 0
    padding: 0
    font-size: 62.5%
    background-color: whitesmoke


body
    margin: 0
    padding: 0
    scroll-behavior: smooth
    background-attachment: fixed
    color: rgb(33,33,33)

.link, a
    text-decoration: none
    color: rgb(33,33,33)
    cursor: pointer

button
    cursor: pointer

.page-section-title
    font-size: 2.4rem
    margin: 3rem 0 3rem 0

.visible
    opacity: 1

h2
    font-size: 2rem

.round
    border-radius: 50%